$primary-color: #1AC073;
$sub-color: #F7F8FA;

.nav {
	position: relative;
	background-color: $primary-color;
	display: flex;
	justify-content: space-between;
	height: 80px;
	& .nav__logo {
		align-self: center;
		position: relative;
		left: 130px;
		color: white;
		font-family: 'Squada One', cursive;
		font-weight: 400;
		font-size: 32px;
		cursor: pointer;
	}
	& .nav__option {
		display: flex;
		flex-direction: row;
		justify-content: space-evenly;
		align-items: center;
		width: 40%;
		& .item {
			text-decoration: none;
			color: white;
			font-family: 'ABeeZee', sans-serif;
			font-size: 18px;
			cursor: pointer;
		}
		& .item:hover {
			opacity: 0.8;
		}
	}
	& .nav__icon {
		position: relative;
		right: 150px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 6%;
		& i {
			font-size: 25px;
			color: white;
			cursor: pointer;
		}
		& i:hover {
			opacity: 0.8;
		}
	}
}

.navbar {
	background-color: $primary-color;
	position: relative;
	width: 100%;
	height: 80px;
	&__logo {
		position: absolute;
		left: 130px;
		top: 20px;
		color: white;
		font-family: 'Squada One', cursive;
		font-weight: 400;
		font-size: 32px;
		cursor: pointer;
	}
}

@media only screen and (min-width: 738px) and (max-width: 1023px) {
	.nav {
		position: relative;
		background-color: $primary-color;
		display: flex;
		justify-content: space-between;
		height: 80px;
		& .nav__logo {
			align-self: center;
			position: relative;
			left: 70px;
			color: white;
			font-family: 'Squada One', cursive;
			font-weight: 400;
			font-size: 32px;
			cursor: pointer;
		}
		& .nav__option {
			display: flex;
			flex-direction: row;
			justify-content: space-evenly;
			align-items: center;
			width: 60%;
			& .item {
				text-decoration: none;
				color: white;
				font-family: 'ABeeZee', sans-serif;
				font-size: 18px;
				cursor: pointer;
			}
			& .item:hover {
				opacity: 0.8;
			}
		}
		& .nav__icon {
			position: relative;
			right: 70px;
			display: flex;
			justify-content: space-between;
			align-items: center;
			width: 6%;
			& i {
				font-size: 25px;
				color: white;
				cursor: pointer;
			}
			& i:hover {
				opacity: 0.8;
			}
		}
	}
}